<template>
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>学生个人信息</span>
          <el-button class="button" text @click="待完成()">修改个人信息</el-button>
        </div>
      </template>
      <div>{{ translate.user_name }} : {{ data.user_name }}</div>
      <div>{{ translate.gender }} : {{ data.gender }}</div>
      <div>{{ translate.nation }} : {{ data.nation }}族</div>
      <div>{{ translate.birthday }} : {{ data.birthday }}</div>
      <div>{{ translate.native_place }} : {{ data.native_place }}</div>
      <div>{{ translate.phone_number }} : {{ data.phone_number }}</div>
      <div>{{ translate.department_name }} : {{ data.department_name }}</div>
      <div>{{ translate.major_name }} : {{ data.major_name }}</div>
      <div>{{ translate.enrollment_date }} : {{ data.enrollment_date }}</div>
      <div>{{ translate.graduate_date }} : {{ data.graduate_date }}</div>
    </el-card>
  </template>



<script>
import student_api from '@/api/student/student-api'
import student_function from '@/api/student/student-function'
export default{
    data(){
        return{
            user_id:"",
            translate:{
                "user_name":"姓名",
                "gender":"性别",
                "nation":"民族",
                "native_place":"籍贯",
                "phone_number":"电话",
                "major_name":"专业",
                "department_name":"院系",
                "birthday":"生日",
                "graduate_date":"毕业日期",
                "enrollment_date":"入学日期",
            },
            data:{},
            
        }
    },
    mounted() {
    this.user_id = this.$route.query.user_id
    student_function.身份校验检查(this,this.user_id)


    student_api.获取个人信息({user_id:this.user_id}).then(resp=>{
        if(resp.status_code == 200){
            this.data = resp.data;
        }
        else{
          student_function.Alert_Error(this,resp.data)
        }

    })
  },
  methods:{
    待完成(){
        this.$message("待完成")
    },

  }


}
</script>